<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Vehicle Type
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">VehicleType</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add VehicleType</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Vehicle Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Vehicle Name" v-model="vehiclename" v-autofocus required>
                  </div>
                  <div class="row">
                      <div class="col-md-4">
                    <label for="exampleInputEmail1">Length (in ft.)</label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="length" placeholder="Length" required></span></div>
                 <div class="col-md-4">
                    <label for="exampleInputEmail1">Breadth (in ft.)</label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="breadth" placeholder="Breadth" required></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Height (in ft.)</label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="heightt" placeholder="Height" required></span></div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Description" v-model="desc" >
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'VehicleTypeadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', vehiclename: '', length: '', breadth: '', heightt: '', desc: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/VehicleType/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/VehicleType/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.vehiclename = response.data[0]['VehicleName']
          this.length = response.data[0]['Length']
          this.breadth = response.data[0]['Breadth']
          this.heightt = response.data[0]['Height']
          this.desc = response.data[0]['Description']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/common/VehicleType/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/VehicleType/update' }
      this.item.id = this.id
      this.item.vehiclename = this.vehiclename
      this.item.length = this.length
      this.item.breadth = this.breadth
      this.item.heightt = this.heightt
      this.item.desc = this.desc
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'VehicleTypevw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



